<template>
  <div v-if="hasTemplateData" class="col" @submit.prevent>
    <div v-if="hasRepeaterValues">
      <div
        v-for="(templateValue, i) in templateValues"
        :key="templateValue.templateId"
        class="mb-2 px-3"
      >
        <div
          v-for="(form, k) in templateValue.controls"
          :key="k"
          class="row bg-light"
          :class="[{ 'fieldset-container': form.type === 'fieldset' }]"
        >
          <label :for="form.name" class="col-2">
            <p v-if="form.type !== 'checkbox'">
              {{ form.label }}
              <span v-if="form.required" class="required">*</span>
            </p>
          </label>
          <div class="col">
            <template v-if="form.type === 'password'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <input
                      :id="form.name"
                      v-model.trim="form.value"
                      class="form-control input-password"
                      :type="form.type"
                      :name="form.name"
                      :disabled="!form.enabled"
                    />
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                  <a href="javascript:void(0)" class="show-pass"
                    ><i class="fa fa-eye-slash"></i
                  ></a>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'number'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <input
                      :id="form.name"
                      v-model.trim="form.value"
                      class="form-control"
                      :type="form.type"
                      :name="form.name"
                      :title="form.label"
                      :min="form.min"
                      :max="form.max"
                      :step="form.step"
                      :disabled="!form.enabled"
                    />
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'date'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <datetime
                      v-model.trim="form.value"
                      :type="form.type"
                      format="dd MMM yyyy"
                      input-class="form-control"
                      :input-id="form.name"
                      :auto="true"
                      :name="form.name"
                      :title="form.label"
                      value-zone="Europe/Sofia"
                      autocomplete="off"
                      :disabled="!form.enabled"
                    ></datetime>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'datetime'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <datetime
                      v-model.trim="form.value"
                      :type="form.type"
                      format="dd MMM yyyy HH:mm a"
                      input-class="form-control"
                      :input-id="form.name"
                      :auto="true"
                      :name="form.name"
                      :title="form.label"
                      value-zone="Europe/Sofia"
                      autocomplete="off"
                      :disabled="!form.enabled"
                    ></datetime>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'paragraph'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <input
                      :id="form.name"
                      v-model.trim="form.value"
                      :type="form.type"
                      class="form-control"
                      readOnly="true"
                      :name="form.name"
                      :disabled="!form.enabled"
                    />
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'text'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <input
                      :id="form.name"
                      v-model.trim="form.value"
                      :type="form.type"
                      class="form-control"
                      :name="form.name"
                      :disabled="!form.enabled"
                    />
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'textarea'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <textarea
                      :id="form.name"
                      v-model.trim="form.value"
                      class="form-control"
                      :name="form.name"
                      :rows="form.rows"
                      :cols="form.cols"
                      :disabled="!form.enabled"
                    ></textarea>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'checkbox'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <b-form-checkbox
                      :id="form.name + i + templateValue.templateId + i"
                      v-model.lazy="form.value"
                      :name="form.name"
                      :disabled="!form.enabled"
                    >
                      {{ form.label }}
                      <span v-if="form.required" class="required">*</span>
                      <span class="error-message">{{ v.errors[0] }}</span>
                    </b-form-checkbox>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'select'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <multiselect
                      v-model.trim="form.selectedValue"
                      :options="form.options"
                      :searchable="true"
                      :show-labels="true"
                      placeholder="Изберете опция"
                      label="label"
                      track-by="label"
                      :disabled="!form.enabled"
                    ></multiselect>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'selectsearch'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <multiselect
                      v-model.trim="form.value"
                      :options="form.options"
                      label="label"
                      track-by="label"
                      placeholder="Изберете опция"
                      :close-on-select="true"
                      :preserve-search="true"
                      :reset-after="false"
                      group-values="groupOptions"
                      group-label="label"
                      :disabled="!form.enabled"
                    ></multiselect>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'multiselect'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <validation-provider
                    v-slot="v"
                    :name="form.name + templateValue.templateId"
                    :vid="form.name + templateValue.templateId"
                    :rules="form.required ? rules : ''"
                  >
                    <multiselect
                      v-model="form.selectedValues"
                      :options="form.options"
                      :multiple="true"
                      :close-on-select="false"
                      :preserve-search="true"
                      placeholder="Изберете една или повече опции"
                      label="label"
                      track-by="label"
                      :reset-after="false"
                      group-values="groupOptions"
                      group-label="label"
                      :group-select="true"
                      :hide-selected="true"
                      :disabled="!form.enabled"
                    ></multiselect>
                    <p class="error-message">{{ v.errors[0] }}</p>
                  </validation-provider>
                </div>
              </div>
            </template>
            <template v-if="form.type === 'fieldset'">
              <div class="row">
                <div :class="'col-' + form.col">
                  <fieldset-template
                    v-if="form.controls.length !== (0 || null || undefined)"
                    :fieldset-template="form"
                    :template-id="templateValue.templateId"
                    rules="required"
                    @set-fieldset="setFieldset"
                  >
                  </fieldset-template>
                </div>
              </div>
            </template>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <div class="d-flex justify-content-end">
              <div v-if="hasRepeaterValues && i + 1 === templateValuesCount">
                <button
                  class="btn btn-warning text-white"
                  @click.prevent="create()"
                >
                  <i class="mdi mdi-plus-box-multiple-outline"></i>
                </button>
                <button
                  class="btn btn-danger text-white"
                  @click.prevent="remove({ id: templateValue.templateId })"
                >
                  <i class="mdi mdi-trash-can-outline"></i>
                </button>
              </div>
              <div v-else>
                <button
                  class="btn btn-danger text-white"
                  @click.prevent="remove({ id: templateValue.templateId })"
                >
                  <i class="mdi mdi-trash-can-outline"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="row">
        <div class="col">
          <div class="d-flex justify-content-end">
            <button
              class="btn btn-warning text-white"
              @click.prevent="create()"
            >
              <i class="mdi mdi-plus-box-multiple-outline"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FieldsetTemplate from '../components/FieldsetTemplate.vue'
import { v4 as uuidv4 } from 'uuid'

export default {
  name: 'FormTemplate',
  components: {
    FieldsetTemplate,
  },
  props: {
    formTemplate: {
      type: Object,
      required: true,
    },
    rules: {
      type: [Object, String],
      default: '',
    },
  },
  data() {
    return {
      template: this.formTemplate,
      templateValues: [],
    }
  },
  computed: {
    hasTemplateData() {
      return !this.$lodash.isEmpty(this.formTemplate)
    },
    hasRepeaterValues() {
      return !this.$lodash.isEmpty(this.template.values)
    },
    templateValuesCount() {
      return this.templateValues.map((el) => el.templateId).length
    },
  },
  watch: {
    formTemplate(prevFormTemplate, newFormTemplate) {
      this.template = newFormTemplate
    },
    template: {
      handler() {
        this.$emit('set-repeaters', this.template)
      },
      deep: true,
    },
  },
  mounted() {
    // assign local templateIds to existing templates
    if (this.hasRepeaterValues) {
      this.assignTemplateId()
    }
  },
  methods: {
    create() {
      this.template.values.push(
        this.$lodash.cloneDeep(this.template.itemTemplate)
      )
      this.assignTemplateId()
    },
    remove({ id }) {
      if (this.hasRepeaterValues) {
        let templateIndex = this.templateValues.findIndex(
          (item) => item.templateId === id
        )
        this.template.values.splice(templateIndex, 1)
        this.assignTemplateId()
      }
    },
    setFieldset(fieldset, templateId) {
      const templateValue = this.templateValues.filter(
        (data) => data.templateId === templateId
      )

      if (!this.$lodash.isEmpty(templateValue)) {
        templateValue[0].controls.filter(
          (data) => data.name === fieldset.name
        )[0].controls = fieldset.controls
      }
    },
    assignTemplateId() {
      let repeaterValues = []
      if (this.hasRepeaterValues) {
        repeaterValues = this.template.values.map((obj) => ({
          // every time assigns new templateIds
          ...(() => {
            if (!this.$lodash.has({ obj }, 'templateId')) {
              return {
                templateId: uuidv4(),
              }
            }
          })(),
          ...obj,
        }))
        this.templateValues = repeaterValues
      }
    },
  },
}
</script>
