<template>
  <div id="app">
    <div class="alert-container">
      <b-alert
        variant="danger"
        dismissible
        fade
        :show="error"
        @dismissed="error = false"
      >
        {{ errorMessage }}
      </b-alert>
    </div>
    <validation-observer v-slot="{ handleSubmit }" :vid="randomId()">
      <form
        v-if="hasFormData"
        class="form"
        @submit.prevent="handleSubmit(submitForm)"
      >
        <div class="bg-light p-3">
          <div
            v-for="form in plainFormData"
            :key="form.name"
            class="form-group row"
          >
            <label
              v-if="
                form.type !== 'checkbox' &&
                form.type !== 'repeater' &&
                form.type !== 'fieldset'
              "
              :for="form.name"
              class="col-2 col-form-label"
            >
              <p>
                {{ form.label }}
                <span v-if="form.required" class="required">*</span>
              </p>
            </label>
            <div class="col">
              <template v-if="form.type === 'input-ajax'">
                <div v-if="form.paramControlName === ''" class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <multiselect
                        v-model.trim="form.displayValue"
                        :options="primaryAjaxFieldsData"
                        label="displayValue"
                        track-by="value"
                        placeholder="Изберете опция"
                        :show-labels="true"
                        :searchable="true"
                        :preserve-search="true"
                        :reset-after="false"
                        :disabled="!form.enabled"
                        @select="selectPrimaryItem"
                      >
                        <template #noOptions=""> Списъкът е празен. </template>
                      </multiselect>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
                <div v-if="form.paramControlName !== ''" class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <multiselect
                        v-model.trim="form.value"
                        :options="secondaryAjaxFieldsData"
                        label="displayValue"
                        track-by="value"
                        placeholder="Изберете опция"
                        :show-labels="true"
                        :searchable="true"
                        :preserve-search="true"
                        :reset-after="false"
                        :disabled="!form.enabled"
                        @select="setSecondaryItem"
                        @remove="resetSecondaryField"
                      >
                        <template #noOptions> Списъкът е празен. </template>
                        <template #noResult> Няма намерени елементи. </template>
                      </multiselect>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'password'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <input
                        :id="form.name"
                        v-model.trim="form.value"
                        class="form-control input-password"
                        :type="form.type"
                        :name="form.name"
                        :title="form.label"
                        :disabled="!form.enabled"
                      />
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <a href="javascript:void(0)" class="show-pass"
                      ><i class="fa fa-eye-slash"></i
                    ></a>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'number'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <input
                        :id="form.name"
                        v-model.trim="form.value"
                        class="form-control"
                        :type="form.type"
                        :name="form.name"
                        :title="form.label"
                        :min="form.min"
                        :max="form.max"
                        :step="form.step"
                        :disabled="!form.enabled"
                      />
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'date'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <datetime
                        v-model.trim="form.value"
                        format="dd MMM yyyy"
                        input-class="form-control"
                        :type="form.type"
                        :name="form.name"
                        :title="form.label"
                        :input-id="form.name"
                        value-zone="Europe/Sofia"
                        autocomplete="off"
                        :auto="true"
                        :disabled="!form.enabled"
                      ></datetime>
                      <p class="error-message">
                        {{ v.errors[0] }}
                      </p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'datetime'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <datetime
                        v-model.trim="form.value"
                        format="dd MMM yyyy HH:mm a"
                        input-class="form-control"
                        :type="form.type"
                        :name="form.name"
                        :title="form.label"
                        :input-id="form.name"
                        value-zone="Europe/Sofia"
                        autocomplete="off"
                        :auto="true"
                        :disabled="!form.enabled"
                      ></datetime>
                      <p class="error-message">
                        {{ v.errors[0] }}
                      </p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'paragraph'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <input
                        :id="form.name"
                        v-model.trim="form.value"
                        class="form-control"
                        :type="form.type"
                        :name="form.name"
                        readOnly="true"
                        :disabled="!form.enabled"
                      />
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'text'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <input
                        :id="form.name"
                        v-model.trim="form.value"
                        class="form-control"
                        :type="form.type"
                        :name="form.name"
                        :title="form.label"
                        :disabled="!form.enabled"
                      />
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'textarea'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <textarea
                        :id="form.name"
                        v-model.trim="form.value"
                        class="form-control"
                        :name="form.name"
                        :rows="form.rows"
                        :cols="form.cols"
                        :title="form.label"
                        :disabled="!form.enabled"
                      ></textarea>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'checkbox'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <b-form-checkbox
                        :id="form.name"
                        v-model="form.value"
                        :name="form.name"
                        :disabled="!form.enabled"
                      >
                        {{ form.label }}
                      </b-form-checkbox>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'select'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <multiselect
                        v-model.trim="form.selectedValue"
                        :options="form.options"
                        :searchable="true"
                        :show-labels="true"
                        placeholder="Изберете опция"
                        label="label"
                        track-by="label"
                        :disabled="!form.enabled"
                      ></multiselect>
                      <small class="form-text bg-danger">{{
                        v.errors[0]
                      }}</small>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'selectsearch'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <multiselect
                        v-model.trim="form.value"
                        :options="form.options"
                        label="label"
                        track-by="label"
                        placeholder="Изберете опция"
                        :preserve-search="true"
                        :reset-after="false"
                        group-values="groupOptions"
                        group-label="label"
                        :disabled="!form.enabled"
                      ></multiselect>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
              <template v-if="form.type === 'multiselect'">
                <div class="row">
                  <div :class="'col-' + form.col">
                    <validation-provider
                      v-slot="v"
                      :name="form.name"
                      :rules="form.required ? 'required' : ''"
                    >
                      <multiselect
                        v-model="form.selectedValues"
                        :options="form.options"
                        :multiple="true"
                        :close-on-select="false"
                        :preserve-search="true"
                        placeholder="Изберете една или повече опции"
                        label="label"
                        track-by="label"
                        :reset-after="false"
                        group-values="groupOptions"
                        group-label="label"
                        :group-select="true"
                        :hide-selected="true"
                        :disabled="!form.enabled"
                      ></multiselect>
                      <p class="error-message">{{ v.errors[0] }}</p>
                    </validation-provider>
                    <p class="mb-0 text-danger small">
                      {{ controlErrors[form.name] }}
                    </p>
                  </div>
                </div>
              </template>
            </div>
          </div>
          <div
            v-for="template in repeaterDataTemplates"
            :key="template.repeaterId"
            class="form-group row repeater-container px-3"
          >
            <label :for="template.name">
              <p>{{ template.label }}</p>
            </label>
            <form-template
              :class="'col-' + template.col"
              :form-template="template"
              rules="required"
              @set-repeaters="setRepeaters"
            ></form-template>
          </div>
          <div
            v-for="fieldsetTemplate in fieldsetDataTemplates"
            :key="fieldsetTemplate.fieldsetId"
            class="form-group row fieldset-container"
          >
            <fieldset-template
              v-if="
                fieldsetTemplate.controls &&
                fieldsetTemplate.controls.length !== 0
              "
              :class="'col-' + fieldsetTemplate.col"
              :fieldset-template="fieldsetTemplate"
              rules="required"
              @set-fieldset="setFieldset"
            >
            </fieldset-template>
          </div>
        </div>
        <div class="mb-3 row">
          <div class="col-12">
            <a href="#" class="btn btn-primary mt-2 message-submit no-submit">
              Запиши
            </a>
            <div class="submit-message">
              <div class="content-box">
                <h3>Сигурни ли сте, че искате да запишете данните?</h3>
                <a href="#" class="btn-yes btn btn-success">Да</a>
                <a href="#" class="btn-no btn btn-danger">Не</a>
              </div>
            </div>
          </div>
          <div class="col-2 submit-btn">
            <button class="btn btn-primary mt-2" type="submit">Запиши</button>
          </div>
        </div>
      </form>
    </validation-observer>
  </div>
</template>

<script>
import FormTemplate from './components/FormTemplate.vue'
import FieldsetTemplate from './components/FieldsetTemplate.vue'
import { v4 as uuidv4 } from 'uuid'
// eslint-disable-next-line no-unused-vars
import { requiredRule } from './validations/validations'

// import {
//   dynamicFormData,
//   ajaxRegisterData,
//   ajaxSecondaryData,
// } from './data/dynamicFormDataAjax'

export default {
  name: 'App',
  components: {
    FormTemplate,
    FieldsetTemplate,
  },
  data() {
    return {
      formData: [],
      repeaterForm: {},
      repeaterTemplates: [],
      cleanRepeaterForm: {},
      repeaterDataTemplates: [],
      fieldsetDataTemplates: [],
      primaryAjaxFieldsData: [],
      secondaryAjaxFieldsData: [],
      selectedField: '',
      submitData: [],
      error: false,
      errorMessage: '',
      controlErrors: {},
      typingTimer: 0, //timer identifier
      doneTypingInterval: 2000, //time in ms (5 seconds)
    }
  },
  computed: {
    hasFormData() {
      return !this.$lodash.isEmpty(this.formData)
    },
    hasRepeaterData() {
      return !this.$lodash.isEmpty(
        this.formData.filter((data) => data.type === 'repeater')
      )
    },
    hasFieldsetData() {
      return !this.$lodash.isEmpty(
        this.formData.filter((data) => data.type === 'fieldset')
      )
    },
    hasRepeaterValues() {
      return (
        this.hasRepeaterData &&
        !this.$lodash.isEmpty(
          this.formData.filter((data) => data.type === 'repeater')[0].values
        )
      )
    },
    hasAjaxFieldsData() {
      return !this.$lodash.isEmpty(this.primaryAjaxFieldsData)
    },
    plainFormData() {
      return this.formData
        .slice(1)
        .filter((data) => data.type !== 'fieldset' && data.type !== 'repeater')
    },
    repeaterTemplatesLength() {
      return this.repeaterTemplates.length
    },
    repeaterTemplateLabels() {
      if (this.hasRepeaterData) {
        return this.formData
          .filter((data) => data.type === 'repeater')
          .map((item) => item.label)
      }
      return []
    },
  },
  created() {
    this.getData()
  },
  methods: {
    async getData() {
      const apiUrl = !this.$lodash.isEmpty(window.jsonData)
        ? window.jsonData
        : {
            jsonUrlControls:
              'https://corporate.orak365.net/SmartRegistry/Users/GetTestRepeater',
            jsonUrlCreateEdit:
              'https://corporate.orak365.net/SmartRegistry/Users/GetTestRepeater',
          }

      try {
        const { data } = await this.axios.get(apiUrl.jsonUrlControls)
        this.formData = data
        this.getAjaxFieldUrl()

        // this.formData = dynamicFormData
        // this.primaryAjaxFieldsData = ajaxRegisterData
        // this.secondaryAjaxFieldsData = ajaxSecondaryData
        console.log('formData', this.formData)

        if (
          !this.$lodash.isEmpty(
            this.formData
              .filter((data) => data.type === 'input-ajax')
              .filter((data) => data.paramControlName !== '')
          )
        ) {
          this.checkSecondaryFieldValue()
        }

        if (this.hasRepeaterData) {
          this.formatRepeaterData()
        }
        if (this.hasFieldsetData) {
          this.formatFieldsetData()
        }
      } catch (e) {
        console.error('Error loading data from the server:', e)
        this.error = true
        this.errorMessage = e.errorMessage
        setTimeout(() => this.resetError(), 5000)
      }
    },
    async submitForm() {
      try {
        this.resetError()
        this.controlErrors = {}

        if (this.hasRepeaterData) {
          let formattedFormData = this.$lodash
            .cloneDeep(this.formData)
            .filter((data) => data.type === 'repeater')[0].values

          if (!this.$lodash.isEmpty(formattedFormData)) {
            for (let i = 0; i < formattedFormData.length; i++) {
              let result = this.$lodash.omit(formattedFormData[i], 'templateId')
              formattedFormData[i] = result
            }
          }

          this.formData.filter((data) => data.type === 'repeater')[0].values =
            formattedFormData

          if (
            this.formData.filter((data) => data.type === 'input-ajax')
              .length !== 0
          ) {
            const priamryField = this.formData
              .filter((data) => data.type === 'input-ajax')
              .filter((data) => data.paramControlName === '')[0]

            this.formData
              .filter((data) => data.type === 'input-ajax')
              .filter((data) => data.paramControlName === '')[0].displayValue =
              priamryField.displayValue.displayValue

            const secondaryField = this.formData
              .filter((data) => data.type === 'input-ajax')
              .filter((data) => data.paramControlName !== '')[0]

            this.formData
              .filter((data) => data.type === 'input-ajax')
              .filter((data) => data.paramControlName !== '')[0].displayValue =
              secondaryField.displayValue.displayValue
          }
        }

        console.log('submitted data', this.formData)

        const apiUrl = !this.$lodash.isEmpty(window.jsonData)
          ? window.jsonData
          : {}

        const { data } = await this.axios.post(
          apiUrl.jsonUrlCreateEdit,
          this.formData
        )

        if (data.status === 'Error' || data.status === 'error') {
          console.log('error response:', data)
          this.error =
            data?.errorMessage != null && data?.errorMessage.trim().length !== 0
          this.errorMessage = data?.errorMessage
          if (data.controlErrors != null && data.controlErrors.length !== 0) {
            data.controlErrors.forEach((error) => {
              this.controlErrors[error.controlName] = error.controlMessage
            })
          }
          setTimeout(() => this.resetError(), 5000)
        } else if (data.status === 'Success' || data.status === 'success') {
          location.assign(data?.redirectUrl)
        }
      } catch (e) {
        console.log('Error submitting form', '\n', e)
        if (!this.$lodash.isEmpty(e.errorMessage)) {
          this.error = true
          this.errorMessage = e.errorMessage
          setTimeout(() => this.resetError(), 5000)
        }
      }
    },
    getAjaxFieldUrl() {
      if (
        !this.$lodash.isEmpty(
          this.formData.filter((data) => data.type === 'input-ajax')
        )
      ) {
        const ajaxField = this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName === '')
          .map((data) => ({
            url: data.ajaxUrl,
            name: data.name,
            value: data.value,
            displayValue: data.displayValue,
          }))[0]

        this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName === '')[0].displayValue = {
          value: ajaxField.value,
          displayValue: ajaxField.displayValue,
        }
        console.log('getAjaxFieldUrl ajaxfields', ajaxField)
        this.getAjaxFieldData(ajaxField)
      }
    },
    // get data to choose from for first field
    async getAjaxFieldData(ajaxField) {
      try {
        console.log('in async getAjaxFieldData fields', ajaxField)
        console.log(
          'in async getAjaxFieldData url',
          `${ajaxField.url}&filterValue=${ajaxField.value}&search=${ajaxField.displayValue}`
        )
        let { data } = await this.axios.get(
          `${ajaxField.url}&filterValue=${ajaxField.value}`
        )
        this.primaryAjaxFieldsData = data
        console.log('getAjaxFieldData this.primaryAjaxFieldsData', data)
      } catch (e) {
        console.log('Error getting ajax input data', e)
      }
    },
    async selectPrimaryItem(selectedOption) {
      try {
        console.log('async selectPrimaryItem selectedOption', selectedOption)
        const secondaryField = this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName !== '')[0]

        const secondaryFieldUrl =
          secondaryField.ajaxUrl + `&filterValue=${selectedOption.value}`

        console.log('secondaryFieldUrl: ' + secondaryFieldUrl)

        let { data } = await this.axios.get(`${secondaryFieldUrl}`)
        // reset second select value, when first select value is changed
        this.resetSecondaryField()

        this.secondaryAjaxFieldsData = data
        // this.secondaryAjaxFieldsData = ajaxSecondaryData
        console.log(
          'this.secondaryAjaxFieldsData',
          this.secondaryAjaxFieldsData
        )
      } catch (e) {
        console.error('Error loading data for second input-ajax')
      }
    },
    setSecondaryItem(selectedOption) {
      console.log('secondary field selected option', selectedOption)
      this.formData
        .filter((data) => data.type === 'input-ajax')
        .filter((data) => data.paramControlName !== '')[0].value =
        selectedOption.value
      this.formData
        .filter((data) => data.type === 'input-ajax')
        .filter((data) => data.paramControlName !== '')[0].displayValue = {
        value: selectedOption.value,
        displayValue: selectedOption.displayValue,
      }
      console.log('formData after selecting secondary field', this.formData)
    },
    async checkSecondaryFieldValue() {
      const secondaryField = this.formData
        .filter((data) => data.type === 'input-ajax')
        .filter((data) => data.paramControlName !== '')[0]

      if (secondaryField.value !== '' && secondaryField.displayValue !== '') {
        const primaryField = this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName === '')[0]

        const selectedOption = {
          value: primaryField.value,
          displayValue: primaryField.displayValue.displayValue,
        }
        console.log(
          'in checkSecondaryFieldValue: selectedOption',
          selectedOption
        )
        try {
          await this.selectPrimaryItem(selectedOption)

          console.log(
            'before check secondaryAjaxFieldsData',
            this.secondaryAjaxFieldsData
          )
          if (!this.$lodash.isEmpty(this.secondaryAjaxFieldsData)) {
            console.log(
              'in checkSecondaryFieldValue this.secondaryAjaxFieldsData',
              this.secondaryAjaxFieldsData
            )
            const secondaryFieldSelectedOptions = {
              value: secondaryField.value,
              displayValue: secondaryField.displayValue,
            }
            console.log(
              'secondaryFieldSelectedOptions',
              secondaryFieldSelectedOptions
            )
            this.setSecondaryItem(secondaryFieldSelectedOptions)
            console.log(
              'after check secondaryAjaxFieldsData',
              this.secondaryAjaxFieldsData
            )
          }
        } catch (e) {
          console.error('Error loading second select options', e)
        }
      }
    },
    formatFieldsetData() {
      this.fieldsetDataTemplates = this.formData
        .filter((data) => data.type === 'fieldset')
        .map((data) => ({
          fieldsetId: uuidv4(),
          ...data,
        }))
    },
    formatRepeaterData() {
      this.repeaterDataTemplates = this.formData
        .filter((data) => data.type === 'repeater')
        .map((data) => ({
          repeaterId: uuidv4(),
          ...data,
        }))
    },
    setRepeaters(template) {
      const repeaterId = template.repeaterId
      const templateIndex = this.repeaterDataTemplates
        .filter((data) => data.type === 'repeater')
        .findIndex((item) => item.repeaterId === repeaterId)

      this.repeaterDataTemplates.filter((data) => data.type === 'repeater')[
        templateIndex
      ] = template

      this.formData.filter((data) => data.name === template.name)[0].values =
        template.values
    },
    setFieldset(fieldset) {
      const fieldsetId = fieldset.fieldsetId

      const fieldsetIndex = this.fieldsetDataTemplates
        .filter((data) => data.type === 'fieldset')
        .findIndex((item) => item.fieldsetId === fieldsetId)

      this.fieldsetDataTemplates.filter((data) => data.type === 'fieldset')[
        fieldsetIndex
      ] = fieldset

      this.formData.filter((data) => data.name === fieldset.name)[0].controls =
        fieldset.controls
    },
    randomId() {
      return uuidv4()
    },
    btnClass(type) {
      switch (type) {
        case 'add':
          return 'btn btn-warning'
        case 'history':
          return 'btn btn-info'
        case 'edit':
          return 'btn btn-warning'
        case 'delete':
          return 'btn btn-danger'
      }
    },
    btnIconClass(type) {
      switch (type) {
        case 'add':
          return 'mdi mdi-eye-outline'
        case 'history':
          return 'mdi mdi-progress-clock'
        case 'edit':
          return 'dripicons-pencil'
        case 'delete':
          return 'dripicons-trash'
      }
    },
    resetSecondaryField() {
      if (
        !this.$lodash.isEmpty(
          this.formData
            .filter((data) => data.type === 'input-ajax')
            .filter((data) => data.paramControlName !== '')
        )
      ) {
        this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName !== '')[0].value = ''
        this.formData
          .filter((data) => data.type === 'input-ajax')
          .filter((data) => data.paramControlName !== '')[0].displayValue = null

        console.log(
          'resetSecondaryField',
          this.formData
            .filter((data) => data.type === 'input-ajax')
            .filter((data) => data.paramControlName !== '')[0]
        )
      }
    },
    resetError() {
      this.error = false
      this.errorMessage = ''
    },
  },
}
</script>
