<template>
  <div v-if="hasTemplateData" class="col">
    <!-- {{ fieldsetControls }} -->
    <!-- :class="[
        item.type !== 'checkbox'
          ? `form-group col-${
              fieldsetControls.length * (12 / fieldsetControls.length)
            }`
          : '',
      ]" -->
    <div v-for="(item, i) in fieldsetControls" :key="i" class="row">
      <label :for="item.name" class="col-2">
        <p v-if="item.type !== 'checkbox'">
          {{ item.label }}
          <span v-if="item.required" class="required">*</span>
        </p>
      </label>
      <div class="col">
        <template v-if="item.type === 'password'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <input
                  :id="item.name"
                  v-model.trim="item.value"
                  class="form-control input-password"
                  :type="item.type"
                  :name="item.name"
                  :disabled="!item.enabled"
                />
                <p>{{ v.errors[0] }}</p>
              </validation-provider>
              <a href="javascript:void(0)" class="show-pass"
                ><i class="fa fa-eye-slash"></i
              ></a>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'number'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <input
                  :id="item.name"
                  v-model.trim="item.value"
                  class="form-control"
                  :type="item.type"
                  :name="item.name"
                  :title="item.label"
                  :min="item.min"
                  :max="item.max"
                  :step="item.step"
                  :disabled="!item.enabled"
                />
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'date'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <datetime
                  v-model.trim="item.value"
                  format="dd MMM yyyy"
                  input-class="form-control"
                  :type="item.type"
                  :name="item.name"
                  :title="item.label"
                  :input-id="item.name"
                  value-zone="Europe/Sofia"
                  autocomplete="off"
                  :auto="true"
                  :disabled="!item.enabled"
                ></datetime>
                <p class="error-message">
                  {{ v.errors[0] }}
                </p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'datetime'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? `${rules}|date` : ''"
              >
                <datetime
                  v-model.trim="item.value"
                  :type="item.type"
                  input-class="form-control"
                  :input-id="item.name"
                  format="dd MMM yyyy HH:MM a"
                  value-zone="Europe/Sofia"
                  autocomplete="off"
                  :disabled="!item.enabled"
                ></datetime>
                <p class="error-message">
                  {{ v.errors[0] }}
                </p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'paragraph'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <input
                  :id="item.name"
                  v-model.trim="item.value"
                  :type="item.type"
                  class="form-control"
                  readOnly="true"
                  :name="item.name"
                  :disabled="!item.enabled"
                />
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'text'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <input
                  :id="item.name"
                  v-model.trim="item.value"
                  :type="item.type"
                  class="form-control"
                  :name="item.name"
                  :disabled="!item.enabled"
                />
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'textarea'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <textarea
                  :id="item.name"
                  v-model.trim="item.value"
                  class="form-control"
                  :name="item.name"
                  :rows="item.rows"
                  :cols="item.cols"
                  :disabled="!item.enabled"
                ></textarea>
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'checkbox'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <b-form-checkbox
                  :id="item.name + fieldset.repeaterId + Date.now()"
                  v-model.lazy="item.value"
                  :name="item.name"
                  :disabled="!item.enabled"
                >
                  {{ item.label }}
                  <span v-if="item.required" class="required">*</span>
                </b-form-checkbox>
                <span class="error-message">{{ v.errors[0] }}</span>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'select'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <multiselect
                  v-model.trim="item.selectedValue"
                  :options="item.options"
                  :searchable="true"
                  :show-labels="true"
                  placeholder="Изберете опция"
                  label="label"
                  track-by="label"
                  :disabled="!item.enabled"
                ></multiselect>
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'selectsearch'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <multiselect
                  v-model.trim="item.value"
                  :options="item.options"
                  label="label"
                  track-by="label"
                  placeholder="Изберете опция"
                  :close-on-select="true"
                  :preserve-search="true"
                  :reset-after="false"
                  group-values="groupOptions"
                  group-label="label"
                  :disabled="!item.enabled"
                ></multiselect>
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
        <template v-if="item.type === 'multiselect'">
          <div class="row">
            <div :class="'col-' + item.col">
              <validation-provider
                v-slot="v"
                :name="fieldsetId + item.name"
                :vid="fieldsetId + item.name"
                :rules="item.required ? rules : ''"
              >
                <multiselect
                  v-model="item.selectedValues"
                  :options="item.options"
                  :multiple="true"
                  :close-on-select="false"
                  :preserve-search="true"
                  placeholder="Изберете една или повече опции"
                  label="label"
                  track-by="label"
                  :reset-after="false"
                  group-values="groupOptions"
                  group-label="label"
                  :group-select="true"
                  :hide-selected="true"
                  :disabled="!item.enabled"
                ></multiselect>
                <p class="error-message">{{ v.errors[0] }}</p>
              </validation-provider>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FieldsetTemplate',
  props: {
    fieldsetTemplate: {
      type: Object,
      required: true,
    },
    templateId: {
      type: String,
      default: null,
    },
    rules: {
      type: [Object, String],
      default: '',
    },
  },
  data() {
    return {
      fieldset: this.fieldsetTemplate,
      fieldsetControls: this.fieldsetTemplate.controls,
    }
  },
  computed: {
    hasTemplateData() {
      return !this.$lodash.isEmpty(this.fieldsetTemplate)
    },
    hasFieldsetControls() {
      return !this.$lodash.isEmpty(this.fieldset.controls)
    },
    fieldsetId() {
      return this.fieldset.fieldsetId
    },
  },
  watch: {
    fieldsetTemplate(prevFieldset, newFieldset) {
      this.fieldset = newFieldset
    },
    fieldsetControls: {
      handler() {
        this.$emit('set-fieldset', this.fieldset, this.templateId)
      },
      deep: true,
    },
  },
  methods: {
    // submitForm() {
    //   console.log('change ', this.fieldset, ', id:', this.templateId)
    // },
    onInput() {
      console.log('date input')
    },
    onBlur() {
      console.log('date blur')
    },
  },
}
</script>
